html {
    font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
}
/*All Page Maybe Used*/
.border-top-red{
    border-top: 2px solid #e54e7e;
}
.border-top-blue{
    border-top: 2px solid #5c6bc0;
}
.category-container{
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.1);
    background: #f9f9f9;
}
.category-container-title{
    background: #ffffff;
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.1);
    font-size: 20px;
    line-height: 25px;
    text-align: center;
    margin-bottom: 0;
    padding: 15px 15px 18px;
}
.category-card-fix{
    margin: 20px 20px 20px;
    box-shadow: 0 0px 0px 0 rgba(0,0,0,.1);
}
.category-img-fix{
    max-height: 50% !important;
}
.category-video-fix{
    margin: 20px 20px;

}
/*blog.app.blade.php*/
.nav-fix {
    position: absolute;
    top :auto;
    margin-left: 5px;
    margin-right: 5px;
}
.float-position{
    bottom: 45px;
    right: 24px;
}
.floating-fix {
    margin-top: 1.0px;
}
/*blog.post.show.blade.php*/
.card-image-fix {
    height: 90%;
}
/*blog.post.create.blade.php*/
.card-panel-container {
    margin-left: 25px;
    margin-right: 25px;
}
.toast-error {
    background-color: #ef5350;
}
/*blog.leftSide.blade.php*/
.slider-section {
    margin-top: 10px;
    margin-bottom: 10px;
}
/*blog.rightSide.blade.php*/
.border-right-red {
    border-right: 5px solid #e54e7e;
}
.border-right-blue {
    border-right: 5px solid #5c6bc0;
}
.border-right-yellow {
    border-right: 5px solid #f57f17;
}
.title-fix{
    margin-bottom: -5px;
}
.collection-fix{
